@import url(../../node_modules/normalize.css/normalize.css);
html,
body {
  height: 100%; }

body {
  background-size: cover;
  background-attachment: fixed;
  box-sizing: border-box;
  font-size: 24px;
  line-height: 150%;
  margin: 0;
  padding: 75px 0;
  text-align: center; }

body::before {
  content: '';
  display: inline-block;
  height: 100%;
  margin-right: -1.25em;
  vertical-align: middle; }

.halfway {
  display: inline-block;
  vertical-align: middle; }

button {
  border: 1px solid #000000;
  border-radius: 3px;
  color: inherit;
  padding: 0 15px;
  font-size: 18px; }
  button:focus {
    outline: none; }
  button.transparent {
    background: transparent; }
  button.ghost {
    background: none;
    border: none;
    text-decoration: underline; }
  button.continue {
    background: none;
    border: none;
    padding: 20px 30px;
    position: absolute;
    bottom: 0;
    margin-left: -39px;
    font-size: 40px; }

h1,
h2,
h3 {
  font-weight: 400; }

h2 {
  cursor: pointer;
  font-size: 24px;
  line-height: 150%; }

.playfair {
  font-family: 'Playfair Display', serif;
  font-size: 20px; }

.code {
  font-family: 'VT323', sans-serif; }

.josefin {
  font-family: 'Voltaire', serif; }

.amatic {
  font-family: 'Amatic SC', serif; }

.pulse {
  margin: 100px;
  display: block;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background: #cca92c;
  cursor: pointer;
  box-shadow: 0 0 0 rgba(204, 169, 44, 0.4);
  -webkit-animation: pulse 2s infinite;
          animation: pulse 2s infinite; }

.pulse:hover {
  -webkit-animation: none;
          animation: none; }

@-webkit-keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4); }
  70% {
    box-shadow: 0 0 0 10px rgba(204, 169, 44, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0); } }

@keyframes pulse {
  0% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0.4); }
  70% {
    box-shadow: 0 0 0 10px rgba(204, 169, 44, 0); }
  100% {
    box-shadow: 0 0 0 0 rgba(204, 169, 44, 0); } }

.article {
  max-width: 675px;
  padding: 100px 0;
  text-align: justify;
  width: 80%; }
  .article em {
    cursor: pointer;
    font-style: normal;
    opacity: 1;
    text-decoration: underline; }
  .article .exhausted {
    cursor: default;
    font-style: italic;
    text-decoration: none; }
  .article .highlight {
    background: #b38cd9;
    opacity: 0.8;
    transition: background 0.4s linear, color 0.4s linear, opacity 0.2s linear; }
    .article .highlight.revealed {
      background: transparent;
      opacity: 1; }
  .article.three::first-letter {
    font-size: 64px; }

.footer-menu {
  background: #f5f5f5;
  border-top: 1px solid #333333;
  bottom: 0;
  color: #333333;
  position: fixed;
  width: 100%;
  left: 0;
  right: 0; }
  .footer-menu small {
    display: block;
    font-size: 12px; }

.bulbs {
  background-image: url("../images/bulbs.png");
  background-position: right top;
  color: #f5f5f5; }

.constellation {
  background-image: url("../images/constellation.png"); }

.mac {
  background-image: url("../images/mac.png"); }

.mars {
  background-image: url("../images/mars.png");
  background-position: center center;
  color: #f5f5f5; }

.mountains {
  background-image: url("../images/mountains.png");
  background-position: right bottom;
  color: #f5f5f5; }

.satellites {
  background-image: url("../images/satellites.png"); }

.underwater {
  background-image: url("../images/underwater.png");
  color: #f5f5f5; }

.unicorn {
  background-image: url("../images/unicorn.png");
  background-position: right bottom; }

.wild {
  background-image: url("../images/wild.png");
  background-position: center center; }

.trophy-case {
  font-size: 12px;
  opacity: 0.2;
  padding: 15px 0;
  position: fixed;
  text-align: center;
  top: 0;
  transition: opacity 0.5s ease-out;
  width: 100%; }
  .trophy-case:hover {
    opacity: 1; }
  .trophy-case .trophy {
    color: #bbbbbb;
    display: inline-block;
    opacity: 0.5;
    pointer-events: none;
    width: 10%; }
    .trophy-case .trophy.polished {
      color: #663399;
      cursor: pointer;
      pointer-events: all; }
    .trophy-case .trophy::before {
      font-size: 32px; }

.hidden {
  display: none;
  opacity: 0.7; }

.selected {
  opacity: 1; }

.pointed {
  cursor: pointer; }

.accent {
  border-top: 5px solid #663399; }

.clickable {
  cursor: pointer;
  opacity: 0.5;
  transition: opacity 0.5s linear; }
  .clickable:hover {
    opacity: 1; }
